  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 100vh;
  max-width: 100vw;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  transition: 0.8s linear;
  width: 100%;
  z-index: 999991;
  opacity: 1;
}

#preloader.loaded {
  opacity: 0;
